@import (reference) "widgets";
@import "ini";
@import "units";
@import "colorStyle";
#container{
  .absPosCenter();
  width: 1000px;height: 700px;
}
body{
  background-color: black;
}

.dvContainer{
  .relative;
  width: 100%;
  height: 100%;
  border: 1px solid #424242;
  box-sizing: border-box;

  .viewer{
    height:100%;
    width: 100%;overflow: hidden;
    box-sizing: border-box;position: relative;
    .viewport{
      width: 100%;height: 100%;
      .relative;
    }
    .seriesWindow{
      float: left;
      position: relative;
      //border:1px solid #777777;
      box-sizing: border-box;overflow: hidden;
    }
    .seriesWindow.checked{
      //border:1px solid #FFFF66;
    }
    .viewportWrapper{float: left;
      box-sizing: border-box;position: relative;
      //border:1px solid #777777;
      .overlay{
        position: absolute;
        color: #44b9b1;
        font-size: 12px;
      }
    }
    .fullScreen{
      position: absolute;
      width: 40px;height: 40px;bottom: 30px;right:15px;z-index: @maskZ - 5000;
      span{
        display: block;.absPosCenter;
        width: 30px;height: 30px;color:white;line-height: 30px;text-align: center;
      }
    }
  }
}

@bottomPannelH:60px;
.viewer{
  .imgGalleryBack{
    overflow-x: hidden;
    overflow-y: auto;
    position: absolute;
    left: 0;
    bottom: @bottomPannelH;
    width: 100%;
  }
  .imgGallery{
    //display: none;
    width: 100%;position: absolute;
    bottom:0;left: 0;
    background-color: black;
    .areaBox{
      //display: inline-block;
      float: left;
      position: relative;
      width: 33%;
      height: 120px;
      padding: 10px 0;text-align: center;box-sizing: border-box;
    }
    .imgBox{
      margin:0 auto;box-sizing: border-box;
      border: 1px solid #8D9192;
      .relative;
      width:100px;
      height:100px;
      .img{height: 100%;width: 100%;}
      .tag{
        position: absolute;right: 10px;top: 10px;width: 50px;
      }
      .tag > .right{
        display: inline-block;float: right;
        font-size: 12px;
        text-align: right;color:#3B9DFF;
      }
      .tagL{
        position: absolute;left: 5px;top: 10px;width: 50px;
      }
      .tagL > .left{
        display: inline-block;float: left;
        font-size: 12px;
        text-align: left;color:#E6A300;
      }
    }
  }
  .pageBar{
    background-color: #0c0c0c;
    width: 100%;height: 50px;overflow: hidden;
  }
  .bottomPannel{
    width: 100%;
    height: @bottomPannelH;
    background-color: #323232;
  }
}
